@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,*{font-family:"Nunito", cursive !important}.matrimony__container{width:100%;max-width:1400px;padding:0 15px;margin:0 auto;margin-top:20px}.mat__darklighttoggle{position:fixed !important;top:10px;right:20px}input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus,input:-webkit-autofill:active{-webkit-box-shadow:none !important;-webkit-transition:none;transition:none}input:-internal-autofill-selected{background:none}input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus,input:-webkit-autofill:active{-webkit-transition:background-color 5000s ease-in-out 0s, color 5000s ease-in-out 0s;transition:background-color 5000s ease-in-out 0s, color 5000s ease-in-out 0s;transition-delay:background-color 5000s, color 5000s}.mat__profilepicview{height:100px;object-fit:contain}.mat__profilepicholder{cursor:pointer}.mat__profilepicremove{color:red}@media only screen and (max-width: 767px){.image-gallery-thumbnails{display:none}}@media only screen and (max-width: 960px){.editProfile .mat__subcaste{padding-left:0px}.editProfile .mat__caste{padding-right:0px}}

.MuiFormControl-root.mat__textbox{margin-top:30px;width:100%}.mat__signupgrid{padding:20px}.mat__signupgrid .MuiFormHelperText-contained{margin-left:0 !important}.mat__signupgrid-left{padding-right:32px}.mat__signupgrid-left .mat__signupgrid{height:100%}.mat__signupgrid-right{padding-left:32px}.mat__caste{padding-right:20px}.mat__subcaste{padding-left:20px}.selected{background:red}.clearboth{width:100%;height:50px}.MuiSelect-select:focus{background:transparent}.mat__custombutgrp{display:block !important}.mat__custombutgrp .MuiToggleButtonGroup-grouped{border-radius:20px !important;margin:15px 15px 0 0;margin-left:0 !important;padding:6px 20px}.MuiSelect-select:focus{background:transparent !important}@-webkit-keyframes autofill{0%,100%{color:#666;background-color:transparent !important}}input:-webkit-autofill{-webkit-animation-delay:1s;-webkit-animation-name:autofill;-webkit-animation-fill-mode:both}.signup__buttons{padding:20px}.signup__buttons:first-child{margin-right:20px}

.fullscreen.image-gallery-content .image-gallery-slide .image-gallery-image{height:100vh !important}.image-gallery-content .image-gallery-slide .image-gallery-image{max-height:calc(100vh - 80px);height:300px;background:black}

